.ball {
  display: inline-block;
  width: 100%;
  height: 100%;
  margin-top: 20%;
  margin-bottom: 25%;
  border-radius: 50%;
  position: relative;
  background: radial-gradient(circle at 50% 40%, #fcfcfc, #efeff1 66%, #9b5050 100%);
  overflow: hidden;
  -webkit-animation: ballGrow 2s ease-out forwards;
          animation: ballGrow 2s ease-out forwards;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.ball:after {
  content: "";
  position: absolute;
  top: 5%;
  left: 10%;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: radial-gradient(circle at 50% 50%, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.8) 14%, rgba(255, 255, 255, 0) 24%);
  -webkit-transform: translateX(-80px) translateY(-90px) skewX(-20deg);
  transform: translateX(-80px) translateY(-90px) skewX(-20deg);
}

.stage {
  width: 300px;
  height: 300px;
  display: inline-block;
  margin: 20px;
  -webkit-perspective: 1200px;
  -ms-perspective: 1200px;
  -o-perspective: 1200px;
  perspective: 1200px;
  -webkit-perspective-origin: 50% 50%;
  -ms-perspective-origin: 50% 50%;
  -o-perspective-origin: 50% 50%;
  perspective-origin: 50% 50%;
}

.number {
  width: 100%;
  text-align: center;
  line-height: 300px;
  font-size: 160px;
  color: blue;
  font-family: 'Roboto', sans-serif;
  -webkit-animation: ballRoll 2s ease-out;
          animation: ballRoll 2s ease-out;
}

.number:after {
  content: attr(data-number);
  position: absolute;
  -webkit-transform: translateX(-75%);
          transform: translateX(-75%);
  opacity: 0;
  animation: numberReveal 0.1s 1.5s reverse forwards;
}

.number:before {
  content: '?';
  position: absolute;
  -webkit-transform: translateX(-25%);
          transform: translateX(-25%);
  -webkit-animation: numberReveal 0.1s 1.5s forwards;
          animation: numberReveal 0.1s 1.5s forwards;
}

@-webkit-keyframes ballRoll {
  0%, 20%, 50% {
    opacity: 1;
    -webkit-transform: translateY(0) rotateX(0) scale(1);
            transform: translateY(0) rotateX(0) scale(1);
  }
  10%, 35%, 75% {
    -webkit-transform: translateY(100%) rotateX(170deg) scale(0.4);
            transform: translateY(100%) rotateX(170deg) scale(0.4);
  }
  11%, 36%, 76% {
    -webkit-transform: translateY(-100%) rotateX(-170deg) scale(0.3);
            transform: translateY(-100%) rotateX(-170deg) scale(0.3);
    opacity: 0;
  }
  
}

@keyframes ballRoll {
  0%, 20%, 50% {
    opacity: 1;
    -webkit-transform: translateY(0) rotateX(0) scale(1);
            transform: translateY(0) rotateX(0) scale(1);
  }
  10%, 35%, 75% {
    -webkit-transform: translateY(100%) rotateX(170deg) scale(0.4);
            transform: translateY(100%) rotateX(170deg) scale(0.4);
  }
  11%, 36%, 76% {
    -webkit-transform: translateY(-100%) rotateX(-170deg) scale(0.3);
            transform: translateY(-100%) rotateX(-170deg) scale(0.3);
    opacity: 0;
  }
  
}

@-webkit-keyframes ballGrow {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes ballGrow {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes numberReveal {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes numberReveal {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}


.stage:nth-child(2) .ball,
.stage:nth-child(2) .number {
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
}

.stage:nth-child(2) .number:after,
.stage:nth-child(2) .number:before {
  -webkit-animation-delay: 1.8s;
          animation-delay: 1.8s;
}

.stage:nth-child(3) .ball,
.stage:nth-child(3) .number {
  -webkit-animation-delay: 0.6s;
          animation-delay: 0.6s;
}

.stage:nth-child(3) .number:after,
.stage:nth-child(3) .number:before {
  -webkit-animation-delay: 2.1s;
          animation-delay: 2.1s;
}

.stage:nth-child(4) .ball,
.stage:nth-child(4) .number {
  -webkit-animation-delay: 0.9s;
          animation-delay: 0.9s;
}

.stage:nth-child(4) .number:after,
.stage:nth-child(4) .number:before {
  -webkit-animation-delay: 2.4s;
          animation-delay: 2.4s;
}

